import {
	Chip,
	FormControl,
	Input,
	makeStyles,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";


export default (props) => {
	const classes = useStyles();
	const [values, setValues] = useState(props.dataOption);
	const [currValue, setCurrValue] = useState("");

	const handleKeyUp = (e) => {
		// console.log(e.keyCode);
		if (e.keyCode == 13 && e.target.value) {
			const splittedVals = e.target.value.split(',').map(item => item.trim()).filter(splited => splited);

			setValues([...props.dataOption, ...splittedVals]);
			props.setDataOption([...props.dataOption, ...splittedVals]);
			props.setdynamicData((orig) => [...orig, ...splittedVals.map(item => {
				return { option: item }
			})])
			setCurrValue("");
		}
	};

	useEffect(() => {
		// console.log(values);
	}, [values]);

	const handleChange = (e) => {
		setCurrValue(e.target.value);
	};

	const handleDelete = (item, index) => {
		if (values.length > 1) {

			let arr = [...values]
			arr.splice(index, 1)
			// console.log(item)
			
			setValues(arr)
			props.setDataOption(arr);
			props.setdynamicData((orig) => {
				const index = orig.map(e => e.option).indexOf(item);
				// console.log(orig)
				// console.log(index)
				if (index > -1) {
					orig.splice(index, 1);
				}
				return orig;
			});
		}
	}

	return (
		<div className="input-option">
			<FormControl classes={{ root: classes.formControlRoot }}>
				<div className={"container"}>
					{values.map((item, index) => (
						<Chip size="small" onDelete={() => handleDelete(item, index)} label={item} />
					))}
				</div>
				<Input
					placeholder="Comma Seperate Multi Value"
					value={currValue}
					onChange={handleChange}
					onKeyDown={handleKeyUp}
				/>
			</FormControl>
		</div>
	);
}

const useStyles = makeStyles((theme) => ({
	formControlRoot: {
		display: "flex",
		alignItems: "center",
		gap: "8px",
		width: "260px",
		flexWrap: "wrap",
		flexDirection: "row",
		border: '2px solid lightgray',
		padding: 4,
		borderRadius: '4px',
		"&> div.container": {
			gap: "6px",
			display: "flex",
			flexDirection: "row",
			flexWrap: "wrap"
		},
		"& > div.container > span": {
			backgroundColor: "gray",
			padding: "1px 3px",
			borderRadius: "4px"
		}
	}
}));
