import "./App.css"
import React, { useState } from "react";
import { Wheel } from "react-custom-roulette";
import MenuIconSideNav from "./MenuIconSideNav";


const defaultData = [
  { option: 'Payodhi' },
  { option: 'Astha' },
  { option: 'Pallavi' },
  { option: 'Kundan' },
  { option: 'Sri' },
  { option: 'Likhith' }
];

export default (props) => {
  const [mustSpin, setMustSpin] = useState(false);
  const [prizeNumber, setPrizeNumber] = useState(0);

  const [dynamicData, setdynamicData] = useState(defaultData);
  const [dataOption, setDataOption] = useState(dynamicData.map((item) => item.option));




  const handleSpinClick = () => {
    if (props.enableSpin) {
      const newPrizeNumber = Math.floor(Math.random() * dynamicData.length);
      setPrizeNumber(newPrizeNumber);
      setMustSpin(true);
    }
  };

  const trimDataFitCircle = (data) => {
    const maxLength = 9
    const tempData = data.map((item) => {
      if (item.option.length > maxLength) {
        return { option: item.option.substring(0, maxLength) + '...' };
      }
      return item;
    })
    return tempData;
  }
  const onStopSpinHandler = () => {
    
    console.log(dynamicData)
    console.log(prizeNumber)
    console.log(dynamicData[prizeNumber].option)
    props.spinCompleted(dynamicData[prizeNumber].option);
    
    if (props.removeOnPick && dynamicData.length > 1 && mustSpin) {
      setTimeout(()=>{
        setdynamicData((orig) => {
          console.log({orig})
          orig.splice(prizeNumber, 1);
          return orig;
        });
        setDataOption(dynamicData.map((item)=> item.option));
      }, 2000);
    }
    setMustSpin(false);
    
  }

  return (
    <>
      <MenuIconSideNav dataOption={dataOption} setDataOption={setDataOption} setdynamicData={setdynamicData}></MenuIconSideNav>
      <div className="wheel-clickable" onClick={handleSpinClick}>
        <Wheel
          mustStartSpinning={mustSpin}
          prizeNumber={prizeNumber}
          data={trimDataFitCircle(dynamicData)}
          outerBorderColor={["#f2f2f2"]}
          outerBorderWidth={[15]}
          innerBorderColor={["#f2f2f2"]}
          radiusLineColor={["#dedede"]}
          radiusLineWidth={[5]}
          textColors={["#ffffff"]}
          fontSize={[25]}
          textDistance={[50]}
          // perpendicularText={[true]}
          backgroundColors={[
            "#F22B35",
            "#F99533",
            "#24CA69",
            "#514E50",
            "#46AEFF",
            "#9145B7"
          ]}
          onStopSpinning={onStopSpinHandler}
        />
      </div>


    </>
  );
}


